import { DividendsEntity, HistoricalPriceEntity, StockInfo } from "./models";

const URL = window.location.origin

export function download_data(market: string, ticker: string): Promise<void | StockInfo> {

  return fetch(`${URL}/data/${market}/${ticker}.json`, {
    method: 'GET',
  })
    .then(response => response.json())
    .then(data => {

      return data as StockInfo
    })
    .catch((error) => {
      console.error('Error:', error);
    });
}

export function get_dividends(market: string, ticker: string): Promise<void | DividendsEntity[] | null | undefined> {
  return download_data(market, ticker)
    .then(response => {
      if (response != null) {
        return response.dividends
      }
    })
    .catch(error => {
      console.log(error)
    })
}

export function get_historical_price(market: string, ticker: string): Promise<void | HistoricalPriceEntity[] | null | undefined> {
  return download_data(market, ticker)
    .then(response => {
      if (response != null) {
        return response.historical_price
      }
    })
    .catch(error => {
      console.log(error)
    })
}